import React from 'react';
import './style/navbar.css';
import AudioButton from './audioButton';
import logo from './assets/logo.png';

function App() {
  return (
    <div className="navbar">
      <img
        className="image-1"
        src={logo}
      />
      <div className="audio-container">
        <div className="only-audio">
          Only Audio
        </div>
        
        <AudioButton />
      </div>
    </div>
  );
}



export default App;