import React, { useEffect, useState } from "react";
import axios from "axios";
import ReactAudioPlayer from 'react-audio-player';



function AudioPlayer({ currId }) {

  console.log("AudioPlayer invoked");

 
  let audio_streams = {};
  let audio;

  const fetchUrl = (currId) => {

    
  console.log("inside player", currId);
    
    axios.get(
      "https://images" +
        ~~(Math.random() * 33) +
        "-focus-opensocial.googleusercontent.com/gadgets/proxy?container=none&url=" +
        encodeURIComponent("https://www.youtube.com/watch?hl=en&v=" + currId)
    ).then((response) => {
      if (response.ok) {
        response.text().then((data) => {
          let regex =
            /(?:ytplayer\.config\s*=\s*|ytInitialPlayerResponse\s?=\s?)(.+?)(?:;var|;\(function|\)?;\s*if|;\s*if|;\s*ytplayer\.|;\s*<\/script)/gmsu;

          data = data.split("window.getPageData")[0];
          data = data.replace("ytInitialPlayerResponse = null", "");
          data = data.replace(
            "ytInitialPlayerResponse=window.ytInitialPlayerResponse",
            ""
          );
          data = data.replace(
            "ytplayer.config={args:{raw_player_response:ytInitialPlayerResponse}};",
            ""
          );

          let matches = regex.exec(data);
          data = matches && matches.length > 1 ? JSON.parse(matches[1]) : false;

          console.log(data);

          let streams = [],
            result = {};

          if (data.streamingData) {
            if (data.streamingData.adaptiveFormats) {
              streams = streams.concat(data.streamingData.adaptiveFormats);
            }

            if (data.streamingData.formats) {
              streams = streams.concat(data.streamingData.formats);
            }
          } else {
            return false;
          }

          streams.forEach(function (stream, n) {
            var itag = stream.itag * 1,
              quality = false;
            console.log(stream);
            switch (itag) {
              case 139:
                quality = "48kbps";
                break;
              case 140:
                quality = "128kbps";
                break;
              case 141:
                quality = "256kbps";
                break;
            }
            if (quality) audio_streams[quality] = stream.url;
          });

          console.log(audio_streams);
          audio = audio_streams["48kbps"]
          
        });
      }
    });

  };

  useEffect(() => {
      fetchUrl(currId);
      console.log("audio");
     },[])

  useEffect(() => {
    fetchUrl(currId)
  },[currId])


  return (
    <div className="audio-player-container">
      <div className="audio-player">
      <ReactAudioPlayer
  src= {audio}
  autoPlay
  controls
/>
        <div className="controls">
          {/* <input type="range" className="timeline" max="100" /> */}
          {/* <button className="sound-button">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="#fff">
    <path fillRule="evenodd" d="M9.383 3.076A1 1 0 0110 4v12a1 1 0 01-1.707.707L4.586 13H2a1 1 0 01-1-1V8a1 1 0 011-1h2.586l3.707-3.707a1 1 0 011.09-.217zM14.657 2.929a1 1 0 011.414 0A9.972 9.972 0 0119 10a9.972 9.972 0 01-2.929 7.071 1 1 0 01-1.414-1.414A7.971 7.971 0 0017 10c0-2.21-.894-4.208-2.343-5.657a1 1 0 010-1.414zm-2.829 2.828a1 1 0 011.415 0A5.983 5.983 0 0115 10a5.984 5.984 0 01-1.757 4.243 1 1 0 01-1.415-1.415A3.984 3.984 0 0013 10a3.983 3.983 0 00-1.172-2.828 1 1 0 010-1.415z" clipRule="evenodd" />
  </svg>
      </button> */}
          <button
            className="player-button"
            onClick={() => {
              console.log("play button clicked");
              // console.log(audio_tag);
             
            }}
          >
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 20 20"
              fill="#fff"
            >
              <path
                fillRule="evenodd"
                d="M10 18a8 8 0 100-16 8 8 0 000 16zM9.555 7.168A1 1 0 008 8v4a1 1 0 001.555.832l3-2a1 1 0 000-1.664l-3-2z"
                clipRule="evenodd"
              />
            </svg>
          </button>
        </div>
      </div>
    </div>
  ); 
}

export default AudioPlayer;
