import React, { useState, useEffect } from "react";
import Item from "./item";
import "./style/item.css";
import Player from "./controller";


// Render a YouTube video player


function App({ api_url,setcurrId }) {
  const [playlistItems, setItem] = useState([]);
  const [pageToken, setPage] = useState("");
  let number = 1;
  let tempStorage = [];

  let nextPage = "";

  async function fetchApi() {
    console.log(nextPage);
    console.log(pageToken);
    console.log(`${api_url}${pageToken}`);
    const data = await fetch(api_url + pageToken).then((response) =>
      response.json()
    );
    nextPage = data.nextPageToken;
    data.items.map(function (item) {
      tempStorage.push({
        id: item.snippet.resourceId.videoId,
        title: item.snippet.title,
        thumbnail: item.snippet.thumbnails.medium.url,
      });
    });
    setItem([...playlistItems, ...tempStorage]);
    setPage(nextPage);
  }

  console.log("outside ", nextPage);

  const addItem = () => {
    fetchApi();
  };

  useEffect(() => {
    fetchApi();
  }, []);

  // useEffect(
  //   ()=>{fetchApi();} , [pageToken]
  //  );

  return (
    <>
    <div className="items-container">
      {playlistItems.map((citem) => {
        return (
          <>
            <Item key={number}
              number={number++}
              v_id={citem.id}
              title={citem.title}
              thumbnail={citem.thumbnail}
              setcurrId={setcurrId}
            />
          </>
        );
      })}

      <button type="button" className="loadMore" onClick={addItem}>
        Load More
      </button>

      
    </div>
    <Player />
    </>
  );
}

export default App;
